import React, { Component } from 'react';
import { Collapsible, CollapsibleItem, Icon } from 'react-materialize';
import { pageChild } from '../services/service';
import ReactHtmlParser, { processNodes, convertNodeToElement, htmlparser2 } from 'react-html-parser';
//import {eventArrow} from '../resources/js/index';
export default class PageChild extends Component {
    constructor(props) {
        super(props);
        this.state = {
            value: []
        };
      //  eventArrow();
    }
    getData() {
        const url = window.location.pathname;
        const id = url.substring(url.lastIndexOf('/') + 1);
        pageChild({ page: id }).then((el) => {
            let data = el.data.data.data;
            this.setState({ value: data });
        });
     
    }
    componentDidMount() {
        this.getData();
    }

    // componentWillUpdate(prevProps, prevState) {
    //     console.log(prevProps, prevState)
    //     const url = window.location.pathname;
    //     const id = url.substring(url.lastIndexOf('/') + 1);
    //     console.log(id)
    // }
    // dispatching an action based on state change
    componentWillReceiveProps(nextProps) {
        this.getData();
    }
    render() {
        //    console.log(this.state.value)
        let result = this.state.value.map((item, i) => {
            return collapsibleItem(item, i);
        });
       // eventArrow();
        return (
            <div className="max-1024">
                <Collapsible accordion className="arrow_right  main-center">
                    {result}
                </Collapsible>
            </div>)
    }
}
let collapsibleItem = (item, i) => {
    // console.log(item)
    let video = '';
    if (item.video) {
        video = <div className="video"> <iframe className ="video-if" src={item.video}></iframe></div>
    }
    return (<CollapsibleItem key={i}
        expanded={false}
    header={<p className="p">{ReactHtmlParser(item.title)}</p>}
        icon={<Icon>keyboard_arrow_right</Icon>}
        node="div"
    >
        <div className="content">
            {ReactHtmlParser(item.content)}
            {
                video
            }
        </div>
    </CollapsibleItem>);
}