import React from 'react';
import Main from '../components/main';
import PageChild from '../pages/PageChild';
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import { Navbar, NavItem, Icon } from 'react-materialize';
export default class Header extends React.Component {
  // eslint-disable-next-line no-useless-constructor
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <div className="navbar-fixed">
        <Navbar
          alignLinks="right" className="bg-green"
          brand={<Link className="brand-logo light btn-menu" to="/">Home</Link>}
          menuIcon={<Icon>menu</Icon>}
          options={{
            draggable: true,
            edge: 'left',
            inDuration: 250,
            onCloseEnd: null,
            onCloseStart: null,
            onOpenEnd: null,
            onOpenStart: null,
            outDuration: 200,
            preventScrolling: true
          }}
        >
          <ul className ="nvbart">
            <li>
              <div className="navbar-fixed bg-green d-sm">
                <nav>
                  <div className="bg-green">
                    <Link to="/" data-target="mobile-nav" replace className="waves-effect  sidenav-trigge btn-menu" ><i className="material-icons">menu</i></Link>

                  </div>
                </nav>
              </div>
            </li>

            <li>
              <div>
                <Link to="/mirror-help" replace className="waves-effect waves-light light text-green">Mirror Help</Link>
                <div className="divider"></div>
              </div>
            </li>
            <li>
              <div>
                <Link to="/feature-help" replace className="waves-effect waves-light light text-green">Feature Help</Link>
                <div className="divider"></div>
              </div>
            </li>
          </ul>
        </Navbar>
      </div>
      //   <div className="navbar-fixed">
      //   <nav>
      //     <div className="nav-wrapper bg-green">
      //       <a href="/" className="brand-logo logo" >Home</a>
      //       <ul className="right hide-on-med-and-down">
      //         <li><Link to="/mirror-help"   replace >Mirror Help</Link></li>
      //         <li><Link to="/feature-help"   replace >Feature Help</Link></li>
      //       </ul>


      //     </div>
      //   </nav>
      // </div >
    );
  }
}