import React from 'react';
import {
    // BrowserRouter as Router,
    // Switch,
    // Route,
    Link
} from "react-router-dom";
import { reactLocalStorage } from 'reactjs-localstorage';
// import M from 'materialize-css';
import { Collapsible, CollapsibleItem, Icon } from 'react-materialize';
import { categories } from '../services/service';
//import {eventArrow} from  '../resources/js/index';
export default class Main extends React.Component {
    // eslint-disable-next-line no-useless-constructor
    constructor(props) {
        super(props);
        this.state = {
            data: []
        };
        //  eventArrow();
    }
    getData() {
        categories().then((data) => {
            this.setState({ data: data.data.data });
        });
    }
    componentDidMount() {
        this.getData();
    }
    // dispatching an action based on state change
    componentWillReceiveProps(nextProps) {
        this.getData();
    }
    render() {
        let data = this.state.data;
        data = data.map((item, i) => { return collapsibleItem(item, i); });
        try {
            setTimeout(() => {
                let t = document.querySelector('.arrow_right>li:first-child>.collapsible-header.active');
                if(t)
                t.click();
            }, 500);
        } catch (error) {

        }
        // eventArrow();
        return (
            <div className="max-1024">
                <Collapsible accordion className="arrow_right  main-center">
                    {data}
                </Collapsible>
            </div>
        );
    }
}

let links = (key, title, category) => {
    function handleClick(event) {
        let data = event.target.dataset;
        reactLocalStorage.clear();
        reactLocalStorage.setObject('var', data);
    }
    let i = Math.floor(Math.random() * 10000000000) - 254;
    return (<Link to={
        {
            pathname: '/' + key,
            state: {
                key, category
            }
        }
    } data-category={category} data-key={key} location={key} key={i} onClick={handleClick} >{title}</Link>);
}

let collapsibleItem = (item, i) => {
    // console.log(item)
    if (!item) return;
    //console.log(i)
    let check = false;
    if (i <= 0) { check = true; }
    //console.log(check)
    return (
        <CollapsibleItem key={i}
            expanded={check}
            header={item.category}
            icon={<Icon>keyboard_arrow_right</Icon>}
            node="div"
        >
            <div >
                {item.data.map((it) => {
                    let value = {};
                    value = it.value;
                    //  console.log(it)
                    if (value)
                        return links(it.key, value['title'], item.category);
                })}
            </div>
        </CollapsibleItem>
    );
}
