let removeClassActive = (array) => {
    array.forEach(element => {
        let y =element.querySelector('.collapsible-header i.material-icons').classList.remove("active")
        console.log(y)
        element.querySelector('.collapsible-header>i.material-icons').classList.remove("active");
    });

};
let eventArrow = () => {
    setTimeout(() => {
        let array = document.querySelectorAll('ul.arrow_right>li');
        if (array) {
            array.forEach(element => {
                setTimeout(() => {
                    element.addEventListener('click', (event) => {
                           removeClassActive(array);    
                        try {
                            event.target.querySelector('.material-icons').classList.add('active');
                        } catch (error) {
                            element.click();
                        }
                    });
                }, 20);
            });
        }
    }, 10);
};
module.exports = {
    eventArrow
};