import React from 'react';
import { detail } from '../services/service';
import { Link } from "react-router-dom";
import ReactHtmlParser, { processNodes, convertNodeToElement, htmlparser2 } from 'react-html-parser';
import {reactLocalStorage} from 'reactjs-localstorage';
export default class Detail extends React.Component {
    // eslint-disable-next-line no-useless-constructor
    constructor(props) {
        super(props);
        this.state = {
            value: {}
        };
       
    }
    componentDidMount() {
        const data = reactLocalStorage.getObject('var');
       
        detail(data).then((e) => {
            let data = e.data.data.data;
            this.setState({ value: data.value });
        });
    }

    render() {
        // console.log(this.state);
        let result = this.state.value;
        let linkapp = <p>
            <a href={result.linkApp} className="linkApp margin-bottom-24 margin-top-24" target="_blank">{result.linkApp}</a>
        </p>
        if (result.linkApp == null) {
            linkapp = null
        }
        let ps = <div className="ps">{result.ps}</div>;
        if (result.ps == null) {
            ps = null;
        }
        let video = null;
        if (result.video != null) {
            video = <div className="video"> <iframe className="video-if" src={result.video}></iframe></div>
        }
        return (
            <div className="detail">
                <h2 className="title">{result.title}</h2>
                <div className="content">  {ReactHtmlParser(result.content)}</div>
                {linkapp}
                {ps}
                {video}
                <div className="divider margin-bottom-24 margin-top-24"></div>
                <Link to="/" className="btn faq btn-large">FAQ Home</Link>
            </div>
        );
    }
}